.show-more {
    /* margin-right: 5em; */
    cursor: pointer;
    text-align: right;
    color: darkblue;
    font-weight: bold;
}

.show-compact {
    /* margin-right: 5em; */
    cursor: pointer;
    text-align: right;
    color: darkblue;
    font-weight: bold;
}

.show-more:hover,
.show-compact:hover {
    text-decoration: underline;
}

.select__option--is-selected{
    scroll-margin: -5em;
}

.rating-panel{
    font-size: 1.1em;
    background-color: #ddd;
}




.no-option-placeholder{
    text-align: center;
    font-size: 1.1em;
    background-color: #dddddd49;
    padding: 0.5em;
    margin: 0.5em;
    border-radius: 2px;
}

.viewCategoryTitle-undecided {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgba(156, 143, 150, 0.6);
    height: 30px;
    align-items: center;
}

.viewCategoryTitle-Positive {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #a6c29bbf;
    padding: 0px;
    align-items: center;
}

.viewCategoryTitle-Negative {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #EBC57C;
    padding: 0px;
    align-items: center;
}

.viewCategoryTitle-Neutral {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #A6C2CE;
    padding: 0px;
    align-items: center;
}
  
.viewCategoryTitle-title {
    margin: 0;
    font-size: 1.1em;
    font-weight: 20;
}
  
.reorder {
    /* width: 85px; */
    height: 20px;
    font-size: 14px;
    background-color: #ffffff;
    color: #27AAFE;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 20px;
    text-align: center;
    display: flex;
    align-items: center;
}  

.reorder:hover {
    background-color: #27AAFE;
    color: white;
}


  .reorder-wrapper {
    position: relative;
    display: inline-block;
  }
    
