.item-wrapper {
    width: 99.25%;
    height: 5em;
    left: 6px;
    /* top: 2227px; */
    background: #FFFFFF;
    display: flex;
    background-color: #fff;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    /* padding: 10px; */
    margin: 6px;
}

.item-wrapper.empty {
    text-align: center;
}

/* .organize .item-wrapper{
    height: 4em;
} */

.item-wrapper.isDraggingtrue {
    width: 30%;
}

.item-wrapper.organize.Positive, .item-wrapper.organize.Negative, .item-wrapper.organize.Neutral {
    width: 95%;
}

.item-wrapper.vote{
    width: 98.5%;
}

.draggable-area {
    display: flex;
    flex-direction: row;
}

.draggable-column-1 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 34px 1px;
    gap: 2px;
    min-width: 0.25em;
    max-width: 0.25em;
    height: 0.75em;
    border-radius: 4px 0px 0px 4px;
    /* height: 86px; */
    background-color: red;
    background: #EFEFEF;
}

.draggable-column-2 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 34px 1px;
    gap: 2px;
    min-width: 0.25em;
    max-width: 0.25em;
    height: 0.75em;
    background: #EFEFEF;
}

.circle {
    width: 2.5px;
    height: 2.5px;
    border-radius: 25%;
    background: #808080;
}

.grabbable {
    cursor: move; /* fallback if grab cursor is unsupported */
    cursor: grab;
    cursor: -moz-grab;
    cursor: -webkit-grab;
}

.Dropdown-placeholder {
    display: flex;
    align-items: bottom;
}


.organizer-info {
    width: 75%;
    justify-content: flex-start;
    margin: 8px;
    margin-right: 0px;
}

