.organizer-info-title {
    font-size: 18px;
    font-weight: 500;
    color: #000000;
    padding-bottom: 4px;
    padding-right: 4px;
}

.organizer-info-des{
    font-size: 14px;
    color: #000000;
    padding-bottom: 4px;
}

.organizer-info-des-light{
    font-size: 14px;
    color: hsl(0, 0%, 60%);
    padding-bottom: 4px;
}

.isDraggingtrue {
    width: 30%;
}

.vote-current-state {
    width: 25%;
    margin: 0.5em;
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.vote-current-state .vote,
.vote-current-state .cost {
    width: 50%;
    text-align: center;
}