.rmc-picker,
.rmc-multi-picker {
    height: 90px;
    /*34*7*/
}

.rmc-multi-picker {
    display: flex;
    align-items: center;
}

.rmc-picker-item {
    gap: 1rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    font-size: 0.8em;
    height: 27.5px;
    line-height: 27.5px;
    padding: 0 10px;
    white-space: nowrap;
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #9b9b9b;
    width: 100%;
    box-sizing: border-box;
}

.rmc-picker {
    display: block;
    position: relative;
    overflow: hidden;
    width: 100%;
    flex: 1;
    text-align: center;
}

.rmc-picker-mask {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    margin: 0 auto;
    width: 100%;
    z-index: 3;
    background-image: linear-gradient(to bottom,
            rgba(255, 255, 255, 0.95),
            rgba(255, 255, 255, 0.6)),
        linear-gradient(to top, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
    background-position: top, bottom;
    background-size: 100% 204px;
    background-repeat: no-repeat;
}

.rmc-picker-content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;
}

.rmc-picker-indicator {
    box-sizing: border-box;
    width: 80%;
    height: 27.5px;
    position: absolute;
    left: 0em;
    top: 102px;
    z-index: 3;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}
.picker {
    height: 30px;
}

.triangle-buttons {
    /* Center the content */
    align-items: center;
    display: flex;
    justify-content: center;

    /* Spacing */
    padding: 0.5rem;
}

.triangle-buttons__triangle {
    border-style: solid;
    height: 0px;
    width: 0px;
}

.triangle-buttons__triangle--t {
    border-color: transparent transparent #d1d5db;
    border-width: 0 0.5rem 0.5rem;
}

.triangle-buttons__triangle--r {
    border-color: transparent transparent transparent #d1d5db;
    border-width: 0.5rem 0 0.5rem 0.5rem;
}

.triangle-buttons__triangle--b {
    border-color: #d1d5db transparent transparent;
    border-width: 0.5rem 0.5rem 0;
}

.triangle-buttons__triangle--l {
    border-color: transparent #d1d5db transparent transparent;
    border-width: 0.5rem 0.5rem 0.5rem 0;
}

/* Additional adjustments */
.picker-center-container {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.rmc-picker {
    position: absolute;
    overflow: visible;
    top: 1.5em;
}

.my-picker-view-item{
    font-size: 0.8em;
    right: -0.8em;
}


.rmc-picker-item-selected {
    color: #000;
    font-size: 1.1em;
    right: 0.75em;
}


.horizontal-space {
    width: 20px; /* specify the width in pixels */
    height: 0; /* set height to 0 */
}

.picker-container{
    position: relative;
    height: 4em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 25%;
    margin: 0.5em;    
}


.picker-arrow-container{
    z-index:5;
    position: absolute;
    top: 0.3em;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
}

.triangle-buttons__triangle {
    display: inline-flex;
    align-items: center;
    transform: translateX(-4.5em);
}

.picker-arrow-container.bottom {
    top: auto;
    bottom: 0;
  }

/* adjust the positioning of the picker to center it vertically */
.rmc-picker {
    position: absolute;
    overflow: visible;
    top: 57%;
    transform: translateY(-50%);
  }
  
  .triangle-buttons__triangle {
    display: inline-flex;
    align-items: center;
  }

  .triangle-disabled{
    display: none;
  }