.button {
    background-color: #fff;
    border: 1px solid #999;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .button:hover {
    background-color: #ddd;
  }

  #submit.green {
    background-color: green;
  }
  
  #submit.red {
    background-color: red;
  }
  
  #submit.disabled {
    background-color: gray;
  }

  
  .button-wrapper {
    position: relative;
    display: inline-block;
  }
  
  .tooltip {
    position: absolute;
    left: 50%;
    bottom: calc(100%);
    transform: translateY(-50%);
    background-color: #555;
    color: #fff;
    padding: 5px 10px;
    border-radius: 4px;
    white-space: nowrap;
    font-size: 14px;
    z-index: 10;
  }